.Corporate {
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr;
  grid-gap: 10px 10px;
}

.One {
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-template-columns: 1fr;
  grid-gap: 10px 10px;
}

.Two {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr;
  grid-gap: 10px 10px;
}

.Loading {
  padding: 1rem;
  height: 90vh;
  width: 90vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: var(--secondary-font);
}

.Loading h1 {
  color: var(--primary-blue);
}

.Loading h1,
.Loading h3 {
  text-align: center;
}

.Image {
  width: 100%;
}

.Title {
  font-size: 1.3rem;
  color: var(--primary-blue);
  padding: 1rem;
  font-family: var(--primary-font);
}

.Text {
  font-family: var(--primary-font);
  font-size: 1rem;
  padding: 0 1rem;
}

.LineBreak {
  background-color: var(--primary-blue);
}

@media (min-width: 768px) {
  .Corporate {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    justify-items: center;
    align-items: center;
  }

  .One {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    justify-items: center;
    align-items: center;
    margin-bottom: 2rem;
  }

  .Two {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    justify-items: center;
    align-items: center;
    margin-bottom: 6rem;
  }

  .Title {
    font-size: 2rem;
  }

  .Text {
    font-size: 1.3rem;
  }
}

@media (min-width: 1200px) {
  .Title {
    font-size: 2.3rem;
  }
  .Text {
    font-size: 1.6rem;
  }
}
